  src: url('../fonts/proximanova-regular-webfont.eot');
  src: local('☺'), url('../fonts/proximanova-regular-webfont.woff') format('woff'), url('../fonts/proximanova-regular-webfont.ttf') format('truetype'), url('../fonts/proximanova-regular-webfont.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Proxima Nova';
  src: url('../fonts/proximanova-bold-webfont.eot');
  src: local('☺'), url('../fonts/proximanova-bold-webfont.woff') format('woff'), url('../fonts/proximanova-bold-webfont.ttf') format('truetype'), url('../fonts/proximanova-bold-webfont.svg') format('svg');
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: 'Proxima Nova';
  src: url('../fonts/proximanova-extrabold-webfont.eot');
  src: local('☺'), url('../fonts/proximanova-extrabold-webfont.woff') format('woff'), url('../fonts/proximanova-extrabold-webfont.ttf') format('truetype'), url('../fonts/proximanova-extrabold-webfont.svg') format('svg');
  font-weight: 800;
  font-style: normal;
}
/*** Arno Pro ***/
@font-face {
  font-family: 'Arno Pro';
  src: url('../fonts/arnopro-light-display-webfont.eot');
  src: local('☺'), url('../fonts/arnopro-light-display-webfont.woff') format('woff'), url('../fonts/arnopro-light-display-webfont.ttf') format('truetype'), url('../fonts/arnopro-light-display-webfont.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Arno Pro';
  src: url('../fonts/arnopro-light-italic-display-webfont.eot');
  src: local('☺'), url('../fonts/arnopro-light-italic-display-webfont.woff') format('woff'), url('../fonts/arnopro-light-italic-display-webfont.ttf') format('truetype'), url('../fonts/arnopro-light-italic-display-webfont.svg') format('svg');
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: 'Arno Pro';
  src: url('../fonts/arnopro-bold-caption-webfont.eot');
  src: local('☺'), url('../fonts/arnopro-bold-caption-webfont.woff') format('woff'), url('../fonts/arnopro-bold-caption-webfont.ttf') format('truetype'), url('../fonts/arnopro-bold-caption-webfont.svg') format('svg');
  font-weight: bold;
  font-style: normal;
}
/* ==========================================================================
   BASE STYLES
   ========================================================================== */
* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
html {
  height: 100%;
  font: 18px/32px 'Proxima Nova', Arial, sans-serif;
  color: #000;
  overflow-x: hidden;
  background: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html.nav-visible {
  overflow: hidden;
}
body {
  min-height: 100%;
  min-width: 320px;
  position: relative;
  overflow-x: hidden;
  background: #fff;
}
.nav-visible body {
  overflow: hidden;
}
/* ==========================================================================
   _TEXT STYLES
   ========================================================================== */
p,
ul,
ol,
dl {
  margin-top: 16px;
  margin-bottom: 16px;
}
@media (min-width: 750px) {
  p,
  ul,
  ol,
  dl {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}
:focus {
  outline: -webkit-focus-ring-color auto 0;
}
a {
  color: #000;
  outline: none;
  text-decoration: none;
  transition: color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
a:hover {
  outline: none;
  text-decoration: underline;
}
figure {
  margin: 30px 0;
}
figure img {
  max-width: 100%;
  height: auto;
  display: block;
}
audio,
canvas,
img,
video {
  vertical-align: middle;
}
/*** HEADINGS ***/
h1,
.h1 {
  margin-top: 40px;
  margin-bottom: 5px;
  font: 800 32px 'Arno Pro', serif;
  color: #162a49;
}
@media (min-width: 750px) {
  h1,
  .h1 {
    margin-top: 50px;
    font-size: 64px;
  }
}
@media (min-width: 1170px) {
  h1,
  .h1 {
    font-size: 80px;
    line-height: 80px;
  }
}
h2,
.h2 {
  margin-top: 28px;
  margin-bottom: 4px;
  font: 800 34px/34px 'Arno Pro', serif;
  color: #162a49;
}
@media (min-width: 750px) {
  h2,
  .h2 {
    margin-top: 46px;
    font-size: 46px;
    line-height: 46px;
  }
}
@media (min-width: 1170px) {
  h2,
  .h2 {
    font-size: 55px;
    line-height: 55px;
  }
}
h3,
.h3 {
  margin-top: 40px;
  margin-bottom: 16px;
  font: bold 30px/34px 'Arno Pro', serif;
  color: #162a49;
}
@media (min-width: 750px) {
  h3,
  .h3 {
    margin-top: 46px;
    margin-bottom: 30px;
    font-size: 34px;
    line-height: 36px;
  }
}
h4,
.h4 {
  margin-top: 30px;
  margin-bottom: 0;
  font: bold 20px/26px 'Proxima Nova', Arial, sans-serif;
  color: #162a49;
}
@media (min-width: 750px) {
  h4,
  .h4 {
    margin-top: 46px;
    margin-bottom: 16px;
    font-size: 24px;
    line-height: 28px;
  }
}
h5,
.h5 {
  margin-top: 30px;
  margin-bottom: 0;
  font: 300 18px/21px 'Proxima Nova', Arial, sans-serif;
  color: #162a49;
}
@media (min-width: 750px) {
  h5,
  .h5 {
    margin-top: 46px;
    margin-bottom: 16px;
    font-size: 21px;
    line-height: 24px;
  }
}
h6,
.h6 {
  margin-top: 30px;
  margin-bottom: 0;
  font: 300 12px/16px 'Proxima Nova', Arial, sans-serif;
  color: #162a49;
}
@media (min-width: 750px) {
  h6,
  .h6 {
    margin-top: 46px;
    margin-bottom: 16px;
    font-size: 18px;
    line-height: 21px;
  }
}
h1 + p,
h2 + p,
h3 + p,
h4 + p,
h5 + p,
h6 + p,
.h1 + p,
.h2 + p,
.h3 + p,
.h4 + p,
.h5 + p,
.h6 + p {
  margin-top: 0;
}
/* ==========================================================================
   GENERAL STYLES
   ========================================================================== */
.hidden {
  display: none;
}
.pseudo {
  display: block;
  content: '\0020';
  overflow: hidden;
}
.clearfix {
  display: table;
  content: '\0020';
  clear: both;
}
/** TEXT STYLES **/
.text-large {
  margin-bottom: 30px;
  font: 24px/32px 'Arno Pro', serif;
  letter-spacing: .02em;
}
.text-large + p {
  margin-top: -22px;
}
@media (min-width: 750px) {
  .text-large {
    margin-bottom: 44px;
  }
  .text-large + p {
    margin-top: -36px;
  }
}
@media (min-width: 1170px) {
  .text-large {
    font-size: 28px;
    line-height: 38px;
  }
}
.text-medium {
  margin-bottom: 30px;
  font: 22px/30px 'Arno Pro', serif;
  letter-spacing: .02em;
}
@media (min-width: 750px) {
  .text-medium {
    font-size: 24px;
    line-height: 38px;
  }
}
.text-center {
  text-align: center;
}
/** BORDERED LINK **/
.bordered {
  border-bottom: 1px solid #000;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.bordered:hover {
  border-bottom-color: transparent;
  text-decoration: none;
}
/** NO MARGIN **/
.no-margin {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}
/** MOBILE HIDDEN **/
.mobile-hidden {
  display: none;
}
@media (min-width: 750px) {
  .mobile-hidden {
    display: block;
  }
}
.mobile-hidden-inline {
  display: none;
}
@media (min-width: 750px) {
  .mobile-hidden-inline {
    display: inline;
  }
}
/*** FIGURE ***/
.figure {
  margin-bottom: 20px;
}
.figure figcaption {
  margin-top: 15px;
  display: block;
  font: italic 20px/24px 'Arno Pro', serif;
  color: rgba(0, 0, 0, 0.6);
}
/*** LIST-CHECKED ***/
.list-checked {
  padding: 0;
  list-style: none;
}
.list-checked li {
  margin-bottom: 15px;
  padding-left: 40px;
  position: relative;
}
.list-checked li:before,
.list-checked li:after {
  display: block;
  content: '\0020';
  overflow: hidden;
  width: 3px;
  height: 14px;
  background: #162a49;
  border-radius: 3px;
  transform: rotate(40deg);
  position: absolute;
  left: 15px;
  top: 8px;
}
.list-checked li:after {
  height: 8px;
  transform: rotate(-45deg);
  top: 14px;
  left: 9px;
}
/*** TABLE-RESPONSIVE ***/
.table-responsive {
  width: 100%;
  overflow-x: scroll;
}
/* ==========================================================================
   FORMS RESET
   ========================================================================== */
form,
fieldset,
legend {
  padding: 0;
  border: none;
  margin: 0;
  display: block;
}
/*** FIELDS ***/
input[type=text],
input[type=url],
input[type=tel],
input[type=number],
input[type=color],
input[type=email],
textarea {
  width: 100%;
  padding: 20px 12px 16px;
  border: 1px solid rgba(57, 57, 57, 0.2);
  border-radius: 0;
  display: inline-block;
  vertical-align: middle;
  font: 20px/20px 'Proxima Nova', Arial, sans-serif;
  color: #162a49;
  background: transparent;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  appearance: none;
}
input[type=text]:focus,
input[type=url]:focus,
input[type=tel]:focus,
input[type=number]:focus,
input[type=color]:focus,
input[type=email]:focus,
textarea:focus {
  border-color: #162a49;
}
.form-group input[type=text],
.form-group input[type=url],
.form-group input[type=tel],
.form-group input[type=number],
.form-group input[type=color],
.form-group input[type=email],
.form-group textarea {
  width: 100%;
  max-width: 100%;
}
textarea {
  max-width: 550px;
  height: 120px;
  padding-top: 16px;
  resize: none;
}
@media (min-width: 750px) {
  textarea {
    height: 165px;
  }
}
/* noinspection CssInvalidPseudoSelector */
::placeholder {
  color: #4f5c71;
  opacity: 1;
}
/*** BUTTONS ***/
[type=submit],
[type=reset],
[type=button],
button,
.button {
  padding: 18px;
  border: 2px solid #162a49;
  display: inline-block;
  font: 800 15px/18px 'Proxima Nova', Arial, sans-serif;
  color: #162a49;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  white-space: nowrap;
  background: #fff;
  transition: background-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1), color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  user-select: none;
  appearance: none;
  cursor: pointer;
}
[type=submit]:hover,
[type=reset]:hover,
[type=button]:hover,
button:hover,
.button:hover {
  text-decoration: none;
  color: #fff;
  background: #162a49;
}
[type=submit].button-blue,
[type=reset].button-blue,
[type=button].button-blue,
button.button-blue,
.button.button-blue {
  border-color: #34bffc;
  color: #34bffc;
}
[type=submit].button-blue:hover,
[type=reset].button-blue:hover,
[type=button].button-blue:hover,
button.button-blue:hover,
.button.button-blue:hover {
  color: #fff;
  background: #34bffc;
}
[type=submit].button-white,
[type=reset].button-white,
[type=button].button-white,
button.button-white,
.button.button-white {
  background: transparent;
  border-color: #fff;
  color: #fff;
}
[type=submit].button-white:hover,
[type=reset].button-white:hover,
[type=button].button-white:hover,
button.button-white:hover,
.button.button-white:hover {
  color: #34bffc;
  background: #fff;
}
/* ==========================================================================
   DEFAULT FORM
   ========================================================================== */
.form-group {
  margin-bottom: 20px;
}
.form-group:after {
  display: table;
  content: '\0020';
  clear: both;
}
.form-group:last-child {
  margin-bottom: 0;
}
.form-label {
  display: block;
  font: 14px/20px 'Proxima Nova', Arial, sans-serif;
  color: #000;
  letter-spacing: .1em;
}
@media (min-width: 750px) {
  .form-label {
    margin-bottom: 5px;
    font-size: 21px;
  }
}
.form-file {
  min-height: 38px;
  padding-left: 40px;
  position: relative;
  display: inline-block;
  cursor: pointer;
  font-size: 20px;
}
@media (min-width: 750px) {
  .form-file {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}
.form-file:before {
  display: block;
  content: '\0020';
  overflow: hidden;
  width: 26px;
  height: 26px;
  background: url('../img/sprite/upload.png') 0 0 no-repeat;
  background-size: 26px 26px;
  position: absolute;
  left: 0;
  top: 3px;
}
@media (min-width: 750px) {
  .form-file:before {
    top: 18px;
  }
}
.form-file input[type=file] {
  display: none;
}
.svg-address {
  background: url("../svg/sprite.svg") no-repeat;
  background-position: 48.05194805194805% 0;
}
.svg-address-dims {
  width: 40px;
  height: 40px;
}
.svg-calculate {
  background: url("../svg/sprite.svg") no-repeat;
  background-position: 0 0;
}
.svg-calculate-dims {
  width: 37px;
  height: 52px;
}
.svg-feedback {
  background: url("../svg/sprite.svg") no-repeat;
  background-position: 100% 0;
}
.svg-feedback-dims {
  width: 40px;
  height: 40px;
}
.svg-mail {
  background: url("../svg/sprite.svg") no-repeat;
  background-position: 0 100%;
}
.svg-mail-dims {
  width: 40px;
  height: 40px;
}
.svg-phone {
  background: url("../svg/sprite.svg") no-repeat;
  background-position: 51.94805194805195% 100%;
}
.svg-phone-dims {
  width: 40px;
  height: 40px;
}
.svg-tel {
  background: url("../svg/sprite.svg") no-repeat;
  background-position: 79.20792079207921% 68.42105263157895%;
}
.svg-tel-dims {
  width: 16px;
  height: 16px;
}
/* ==========================================================================
   CONTAINER
   ========================================================================== */
.page {
  max-width: 1920px;
  padding-top: 84px;
}
@media (min-width: 750px) {
  .page {
    padding-left: 64px;
  }
  .page:before {
    display: block;
    content: '\0020';
    overflow: hidden;
    width: 64px;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 30;
    background: #162a49;
    box-shadow: 0 0 15px rgba(22, 42, 73, 0.25);
  }
}
.content,
.wrapper {
  max-width: 890px;
  padding-right: 20px;
  padding-left: 20px;
  margin: 0 auto;
}
@media (min-width: 750px) {
  .content,
  .wrapper {
    max-width: 780px;
    padding-right: 30px;
    padding-left: 30px;
    position: relative;
    left: 12px;
  }
}
@media (min-width: 1170px) {
  .content,
  .wrapper {
    max-width: 910px;
    left: 28px;
  }
}
.content {
  padding-top: 50px;
}
.content > .sections:first-child {
  margin-top: -50px;
}
.content .h1:first-child,
.content .h2:first-child {
  margin-top: 0;
}
.wrapper {
  margin: 0 auto;
}
.wrapper.full {
  max-width: none;
}
.block {
  margin-left: -20px;
  margin-right: -20px;
}
@media (min-width: 750px) {
  .block {
    margin-left: -30px;
    margin-right: -30px;
  }
}
@media (min-width: 750px) {
  .wide {
    margin-left: -50vw;
    margin-right: -50vw;
  }
  .wide .space {
    width: 100vw;
    max-width: 1920px;
    padding-left: 64px;
