    position: relative;
    left: -44px;
  }
}
@media (min-width: 1170px) {
  .wide .space {
    left: -60px;
  }
}
.page:after,
.wrapper:after,
.space:after {
  display: table;
  content: '\0020';
  clear: both;
}
/* ==========================================================================
   HEADER
   ========================================================================== */
.header {
  min-width: 320px;
  height: 84px;
  padding-left: 64px;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 99;
  background: #fff;
  box-shadow: 0 0 15px rgba(22, 42, 73, 0.25);
}
.header .logo {
  height: 51px;
  padding: 10px;
  margin: auto;
  position: absolute;
  top: 16px;
  left: 75px;
}
.header .logo a {
  padding: 10px;
  margin: -10px;
  display: block;
}
.header .logo img {
  width: 211px;
  height: 31px;
  display: block;
}
.header .phone {
  height: 36px;
  margin: 0;
  display: none;
  position: absolute;
  right: 22px;
  top: 23px;
  font-size: 16px;
  line-height: 42px;
  font-weight: bold;
}
@media (min-width: 750px) {
  .header .phone {
    display: block;
  }
}
@media (min-width: 1170px) {
  .header .phone {
    right: 31px;
  }
}
.header .phone a {
  padding-right: 51px;
  position: relative;
  z-index: 5;
  display: block;
  color: #162a49;
}
.header .phone a:hover {
  text-decoration: none;
}
.header .phone .icon {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background: #162a49;
  backface-visibility: hidden;
}
.header .phone .icon:before {
  display: block;
  content: '\0020';
  overflow: hidden;
  width: 16px;
  height: 16px;
  background: url("../svg/sprite.svg") no-repeat;
  background-position: 79.20792079207921% 68.42105263157895%;
  margin: auto;
  position: absolute;
  top: 1px;
  right: 2px;
  bottom: 0;
  left: 0;
}
.header .menu {
  display: none;
  cursor: default;
}
@media (min-width: 1170px) {
  .header .menu {
    padding: 0;
    margin: 0;
    list-style: none;
    display: block;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 325px;
    font-size: 13px;
    line-height: 24px;
    font-weight: bold;
    letter-spacing: .05em;
    text-transform: uppercase;
  }
  .header .menu li {
    float: left;
    position: relative;
  }
  .header .menu li:before {
    display: block;
    content: '\0020';
    display: none;
    height: 3px;
    overflow: hidden;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    background: #009ee3;
  }
  .header .menu li a {
    padding: 33px 21px 27px;
    display: block;
    position: relative;
    z-index: 5;
    transition: color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  .header .menu li a:hover {
    color: #009ee3;
    text-decoration: none;
  }
  .header .menu li.active {
    cursor: default;
  }
  .header .menu li.active:before {
    display: block;
  }
  .header .menu li.active a {
    color: #009ee3;
    cursor: default;
  }
}
/* ==========================================================================
   NAV
   ========================================================================== */
.nav {
  width: 64px;
  height: 84px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 101;
}
.nav-visible .nav {
  width: auto;
  height: auto;
  right: 0;
  bottom: 0;
  overflow: hidden;
  backface-visibility: hidden;
}
.nav .toggle {
  width: 64px;
  height: 84px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 105;
  background: #2c3e5a;
  transition: background-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1), box-shadow 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  box-shadow: 0 0 15px rgba(22, 42, 73, 0.25);
}
.nav .toggle:hover {
  background: #162a49;
}
.nav-open .nav .toggle {
  background: #162a49;
  box-shadow: none;
}
.nav .toggle span,
.nav .toggle:before,
.nav .toggle:after {
  display: block;
  content: '\0020';
  overflow: hidden;
  width: 26px;
  height: 3px;
  margin: auto;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 3px;
  left: 0;
  background: #fff;
  transition: all .3s;
  backface-visibility: hidden;
}
.nav-open .nav .toggle span {
  opacity: 0;
}
.nav .toggle:before {
  transform: translate3d(0, -10px, 0);
}
.nav-open .nav .toggle:before {
  width: 30px;
