/* ZOOM-IN EFFECT */
.mfp-slide-in .mfp-content {
  transition: transform 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
  transform: translate3d(0, -150%, 0);
}
.mfp-slide-in.mfp-bg {
  opacity: 0;
  transition: opacity 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.mfp-slide-in.mfp-ready .mfp-content {
  transform: translate3d(0, 0, 0);
  transition-delay: .2s;
}
.mfp-slide-in.mfp-ready.mfp-bg {
  opacity: .7;
}
.mfp-slide-in.mfp-removing .mfp-content {
  transform: translate3d(0, -150%, 0);
  transition-delay: 0;
}
.mfp-slide-in.mfp-removing.mfp-bg {
  opacity: 0;
  transition-delay: .4s;
}
.mfp-wrap {
  min-width: 320px;
  -webkit-overflow-scrolling: touch;
}
.mfp-container {
  padding-left: 40px;
  padding-right: 40px;
}
.mfp-bg {
  background: rgba(22, 42, 73, 0.7);
}
.mfp-bottom-bar,
.mfp-title,
.mfp-counter {
  color: #fff;
  cursor: default;
}
.mfp-content {
  max-width: 450px;
  position: relative;
}
@media (min-width: 750px) {
  .mfp-content {
    max-width: 650px;
  }
}
@media (min-width: 1170px) {
  .mfp-content {
    max-width: 1140px;
  }
}
.mfp-close {
  width: 40px !important;
  height: 40px;
  padding: 0 !important;
  right: -40px !important;
  text-align: left !important;
  opacity: .75;
  transition: opacity 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  cursor: pointer;
}
.mfp-close:before,
.mfp-close:after {
  display: block;
  content: '\0020';
  overflow: hidden;
  width: 26px;
  height: 3px;
  border: none;
  margin: auto !important;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #fff;
  opacity: 1;
  backface-visibility: hidden;
}
.mfp-close:before {
  transform: rotate(45deg);
}
.mfp-close:after {
  transform: rotate(-45deg);
}
.mfp-close:hover {
  opacity: 1;
  top: 0;
}
.mfp-arrow {
  width: 40px;
  height: 40px;
  margin: auto;
  top: 0;
  bottom: 0;
  opacity: .75;
  transform: scale(1);
  transition: opacity 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  cursor: pointer;
}
.mfp-arrow:before {
  display: block;
  content: '\0020';
  overflow: hidden;
  width: 13px;
  height: 13px;
  border: none;
  border-top: 3px solid #fff;
  border-right: 3px solid #fff;
  margin: auto !important;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 1;
  backface-visibility: hidden;
}
.mfp-arrow:after {
  display: none;
}
.mfp-arrow:hover {
  margin-top: auto !important;
  opacity: 1;
}
.mfp-arrow.mfp-arrow-left {
  left: 0;
}
@media (min-width: 750px) {
  .mfp-arrow.mfp-arrow-left {
    left: 20px;
  }
}
.mfp-arrow.mfp-arrow-left:before {
  left: 5px;
  transform: rotate(-135deg);
}
.mfp-arrow.mfp-arrow-right {
  right: 0;
}
@media (min-width: 750px) {
  .mfp-arrow.mfp-arrow-right {
    right: 20px;
  }
}
.mfp-arrow.mfp-arrow-right:before {
  right: 5px;
  transform: rotate(45deg);
}
